class ProgrammeAcceuil extends Component {
  constructor(props) {
      super(props)
      this.state = {
        test:[],
        ok:"",
        tournoi:[],
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" /></div>,
        status:true,
      }
  }
  
  componentDidMount() {
    const requestOptions = {
      method: 'GET',
     
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data tournoi===>",data)
      this.setState({status:false})
      try{
        this.setState({tournoi:data,status:false})
 
       }catch(error){
        this.setState({tournoi:[]})
       }
     }
     
     );


  };

  render() {
    // const animatedComponents = makeAnimated();
    const matchs=this.props.matchs
    // var today = new Date();
    // var Annee = today.getFullYear()

  return (
    <>

   {this.state.ok}
        <CContainer fluid>
    
  {
        (!this.state.status)?

    (
    <CFade>
   

      
      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">



          {/* Tournoi inter-écoles des écoles secondaires */}

          {/* <div className="mt-5">
            <div class="section-title">
              <h2> du tournoi 'Nom du tournoi'</h2>
            </div>
          </div> */}
          
          {/* <Row className="mb-5">
            <Col>
              <strong><h3  > <i className="bx bxs-download bx-sm " style={{color:"#338C36"}}/> Télécharger le programme des matchs </h3></strong>
              <PDFDownloadLink document={<DocumentPdf/>} fileName={"ressource##"+this.state.ressource.id+".pdf"}>
                  <strong onClick={this.handleTelechargeIncrement}><h3  > <i className="bx bxs-download bx-sm " style={{color:"#338C36"}}/> Télécharger la fiche de la ressource </h3></strong>
              </PDFDownloadLink>
            </Col>
          </Row>  */}

          <Row> 
            <Card>
              
              <CardBody>
                
                {/* <CardTitle tag="h5">{element.titre}</CardTitle>
                <CardSubtitle tag="h6" className="mb-2 text-muted">bouton: {element.bouton}</CardSubtitle>
                <CardText>{element.description}</CardText> */}

{
                  
                  <Row className="mt-4">
                  <Col> 
                    
                    <div   role="form" class="php-email-form" >
                      <h2 className="text-center mb-4">Programme du tournoi</h2>
                      <Row 
                        className="text-center mb-4" 
                        // style={{justifyContent:"center"}}
                        // style={{width:"150px",height:"150px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                      >
                        <div >
                          <a href="/#/" download  style={{color:"#338C36"}}> <i className="bx bxs-download bx-sm" /> Télécharger le programme des matchs</a>
                        </div>
                      </Row>
                      <hr className="text-center mb-5"></hr>
                      <div className="form-row text-center">
                        {
                       (matchs.length)?
                        matchs.map((equipe)=>( 
                        (equipe.repousse=="PROGRAMME")?   
                        <div class="form-group col-md-6">

                          <ListGroup>
                            <ListGroupItem action variant="secondary" className="mb-2">
                              <Row>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.equipeVisites[0].nomEquipe}</p>
                                  </Row>
                                  <Row>
                                    <img src={logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                                <Col>
                                  {/* <center> 
                                    <p style={{textAlign:"center"}}> 
                                      <strong>{equipe.scoreVisite}</strong>
                                      - 
                                      <strong> {equipe.scoreVisiteur} </strong>
                                    </p>
                                  </center> */}
                                  <center> <small style={{textAlign:"center"}}> {equipe.date}</small> </center>
                                  <center> <small style={{textAlign:"center"}}> {equipe.heure} </small> </center>
                                </Col>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.equipeVisiteurs[0].nomEquipe}</p>
                                  </Row>
                                  <Row>
                                    <img src={logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                              </Row>
                            </ListGroupItem>
                          </ListGroup>
                         

                        </div>:<h4 className="text-center" style={{textAlign:"center"}}>Aucun match Programmé pour ce tournoi</h4>)):<h4 className="text-center" style={{textAlign:"center"}}>Aucun match Programmé pour ce tournoi</h4>
                        }

                        
                      </div>
                    </div>
                  </Col>
                </Row>
              }
                

              </CardBody>
            </Card>
          </Row>

                   

        </div>
      </section>

    </CFade>):(<>{this.state.page}</>)
     }
    
    </CContainer>

    </>
  )
}
}

export default ProgrammeAcceuil
